.autoComplete {
	border-radius: 4px;
}

.autoComplete input {
	text-align: center;
	font-size: 20px;
	font-weight: bold;
}

.autoComplete fieldset {
	text-align: center;
}

.autoComplete label {
	width: 100%;
	text-align: center;
	transform-origin: top center;
	left: -13px;
}

.autoComplete .Mui-disabled {
	color: black !important;
}

.autoComplete .MuiOutlinedInput-root {
	padding: 3px !important;
}