.container {
	margin: 0 auto 40px;
}

.container h3 {
	text-align: center;
	font-size: 20px;
}

.kzt_currency {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	align-items: center;

	border-radius: 5px;

	padding: 10px 30px !important;
	margin: 10px auto 20px;

	max-width: 500px;
	background: #434343;
}

.kzt_currency p:last-child {
	justify-self: right;
}

.currency_row {
	display: grid;
	grid-template-columns: repeat(6, 1fr);
	align-items: center;

	padding: 10px 20px;

	background: #434343;
}

.currency_row:nth-child(2n) {
	background: #787878;
}

.currency_row:last-child {
	border-radius: 0 0 5px 5px;
}

.currency_row p,
.kzt_currency p {
	color: white;
	font-weight: bold;
	display: flex;
	gap: 8px;
	align-items: center;
}

.currency_row img,
.kzt_currency img {
	width: 45px;
}

.list_header {
	display: grid;
	grid-template-columns: repeat(6, 1fr);
	align-items: center;

	padding: 10px;

	font-weight: bold;
	color: white;

	border-radius: 5px 5px 0 0;

	background: #434343;

	padding: 10px 20px;
}

.showAll {
	display: flex;
	gap: 5px;
	text-align: center;
	justify-content: center;

	padding: 5px;
	margin-top: 10px;

	border-radius: 10px;

	font-weight: bold;

	cursor: pointer;
}

.showAll:hover {
	background: #80808021;
}