.departmentsList {
	max-width: 1000px;
	margin: auto;

	display: grid;
	grid-template-columns: 1fr auto;
	gap: 30px;
	align-items: center;
}

.department {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 10px;
	align-items: center;
	padding: 15px;
	background-color: white;
	border: 1px solid #fd5f0030;
	border-radius: 5px;
	transition: .5s;
	cursor: pointer;
	box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
		0px 1px 1px 0px rgba(0, 0, 0, 0.14),
		0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.department:hover {
	box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
		0px 5px 8px 0px rgba(0, 0, 0, 0.14),
		0px 1px 14px 0px rgba(0, 0, 0, 0.12);
	background-color: #fffbf8;
}

.department h5 {
	font-size: 22px;
}

.department p {
	justify-self: end;
}

.plus_department {
	background-color: rgb(53, 170, 53);
	color: white;
	padding: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 100%;
	transition: .5s;
	box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
		0px 1px 1px 0px rgba(0, 0, 0, 0.14),
		0px 1px 3px 0px rgba(0, 0, 0, 0.12);
	cursor: pointer;
}

.plus_department:hover {
	background-color: rgb(45 149 45);
	box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
		0px 5px 8px 0px rgba(0, 0, 0, 0.14),
		0px 1px 14px 0px rgba(0, 0, 0, 0.12);
}

.plus_department svg {
	font-size: 40px;
}