.background_color {
	background-color: white;
	box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
		0px 1px 1px 0px rgba(0, 0, 0, 0.14),
		0px 1px 3px 0px rgba(0, 0, 0, 0.12);
	border-radius: 10px;
	overflow: hidden;
}

.container {
	max-width: 1280px;
	width: 100%;

	margin: 0 auto;
	padding: 32px 100px;
}

.container h1 {
	text-align: center;
	margin-bottom: 24px;
}