.container {
	display: flex;
	flex-direction: column;
	gap: 20px;
	padding: 0 53px 15px;
}

.kurs_container {
	display: flex;
	justify-content: center;
	gap: 12px;
}

.kurs_container span {
	font-size: 18px;
	font-weight: 600;
}

.textField {
	background: white,
}

.textField input {
	text-align: center;
	font-size: 18px;
	font-weight: bold;
}

.textField fieldset {
	text-align: center;
}

.textField label {
	width: 100%;
	text-align: center;
	transform-origin: top center;
	left: -13px;
	font-size: 20px;
}

.textField .Mui-disabled {
	color: "black !important",
}