.textField {
	background: white;
}

.swap_currencies_btn {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 12px;

	margin: 5px 0;
}

.swap_currencies_btn span {
	font-size: 18px;
	font-weight: 600;
}

.swap_currencies_btn button {
	background: #005792;
	transition: .3s;
	color: white;
}

.swap_currencies_btn button:hover {
	background: #00406b;
	box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
		0px 2px 2px 0px rgba(0, 0, 0, 0.14),
		0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

@media (max-width: 1200px) {
	.currency_calc_container {
		min-width: 600px;
		justify-self: center;
	}
}

@media (max-width: 900px) {
	.currency_calc_container {
		min-width: 400px;
	}
}

@media (max-width: 600px) {
	.currency_calc_container {
		min-width: 100%;
	}
}