.background_color {
	background-color: #f6f6e9;
}

.container {
	max-width: 1280px;
	width: 100%;

	margin: 0 auto;
	padding: 32px 100px;
}

.container h1 {
	text-align: center;
	margin-bottom: 24px;
}

.btn_group {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 20px;

	max-width: 600px;

	margin: auto;
}

@media (max-width: 900px) {
	.container {
		padding: 32px 20px;
	}

	.container h1 {
		margin-bottom: 12px;
	}
}

@media (max-width: 600px) {
	.btn_group {
		padding: 0 10px;
	}
}