.background_color {
	background-color: #f6f6e9;
}

.container {
	max-width: 1280px;
	width: 100%;

	margin: 0 auto;
	padding: 32px 100px;
}

.container h1 {
	text-align: center;
	margin-bottom: 24px;
}

.reserveCode {
	font-size: 30px;
	font-weight: bold;
	color: white;
	text-align: center;

	background: #ff4b00;

	padding: 10px;

	border-radius: 10px;
}

@media (max-width: 900px) {
	.container {
		padding: 32px 20px;
	}

	.container h1 {
		margin-bottom: 12px;
	}
}

@media (max-width: 600px) {
	.reserveCode {
		font-size: 25px;
		text-align: center;
	}
}