.background_color {
	color: white;
	min-height: 100vh;
	padding-left: 300px;
}

.container {
	max-width: 1280px;
	width: 100%;

	margin: 0 auto;
	padding: 32px 100px;

	overflow: hidden;
}

.container h1 {
	text-align: center;
	color: #e25500;
}

.pdfDoc {
	max-width: 500px !important;
	height: auto !important;
}

@media (max-width: 1200px) {
	.background_color {
		padding-left: 0;
	}
}

@media (max-width: 900px) {
	.container {
		padding: 16px 60px;
	}

	.container h1 {
		margin-bottom: 0;
	}
}

@media (max-width: 700px) {
	.background_color {
		min-height: auto;
	}

	.container {
		padding: 16px 10px;
	}

	.container h1 {
		margin-bottom: 0;
	}

	.pdfDoc {
		max-width: 100% !important;
	}

	.pdfDoc canvas {
		max-width: 100% !important;
		height: unset !important;
	}
}