.footer_container {
	background-color: #EEEEEE;
	position: fixed;
	left: 0;
	top: 0;
	height: 100vh;
	width: 300px;
	padding: 20px;

	display: grid;
	align-content: space-around;
	gap: 20px;
}

.footer_container>div:first-child {
	height: 60px;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
}

.footer_container img {
	width: 400px;
	position: absolute;
}

.footer_container>div:nth-child(2) {
	display: grid;
	align-content: center;
	gap: 20px;
}

.footer_container>div:nth-child(2)>div {
	display: flex;
	align-items: center;
	gap: 10px;
}

.footer_container>div:nth-child(2)>div>svg {
	color: #e25500
}

.footer_container>div:nth-child(2)>div p {
	font-weight: bold;
}

.footer_container>div:nth-child(2)>div a {
	font-weight: bold;
	color: #e25500;
}

.mapBlock {
	width: 100%;
}

@media (max-width: 1200px) {
	.footer_container {
		position: relative;
		height: auto;
		width: 100%;
		overflow: hidden;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
	}

	.footer_container>div:first-child {
		width: 200px;
	}

	.footer_container>div:nth-child(2) {
		grid-template-areas: "tel map"
			"date map"
			"address map"
			". map";
	}

	.footer_container>div:nth-child(2)>div:first-child {
		grid-area: tel;
	}

	.footer_container>div:nth-child(2)>div:nth-child(2) {
		grid-area: date;
	}

	.footer_container>div:nth-child(2)>div:nth-child(3) {
		grid-area: address;
	}

	.footer_container>div:nth-child(2)>div:last-child {
		grid-area: map;
	}

	/* .styles_footer_container>div:nth-child(2) */
}

@media (max-width: 900px) {
	.footer_container {
		justify-content: space-around;
	}

	.footer_container>div:nth-child(2) {
		grid-template-areas: "tel"
			"date"
			"address"
			"map";
	}
}

@media (max-width: 600px) {
	.footer_container {
		flex-direction: column;
	}

	.footer_container>div:first-child {
		width: 150px;
	}

	.footer_container img {
		width: 300px;
		position: absolute;
	}
}