.title {
	display: flex;
	justify-content: center;

	color: black;
	font-size: 22px;
	font-weight: bold !important;

	padding-bottom: 0 !important;
}

.container {
	margin: 0 auto;

	width: 100%;

	box-sizing: border-box;
}

.currency_title {
	margin-bottom: 24px;
}

.currency_title h2 {
	font-size: 45px;
	color: #13334c;
	text-align: center;
}

.currency_title p {
	font-size: 18px;
	text-align: center;
}

.currency_title p:first-child {
	font-size: 24px;
	text-decoration: underline;
}

.competitor_block {
	display: grid;
	justify-content: center;

	border: 1px solid rgb(19, 51, 76);
	border-radius: 5px;

	color: rgb(19, 51, 76);

	padding: 10px;
	margin-bottom: 20px;

	background: white;
}

.competitor_block h2 {
	text-align: center;
	font-size: 25px;

	border: 1px solid #0000004f;
	border-radius: 5px;
}

.rate_block {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 20px;

	font-size: 20px;
}

.rate_text p {
	font-weight: bold;
	text-align: start;
}

.rate_text div {
	text-align: start;
	font-size: 25px;
}

.rate_text div:last-child {
	text-align: end;
}

.currency_calc {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 20px;

	background: #00587A;

	border-radius: 5px;

	padding: 20px;
	margin-bottom: 20px;
}

.currency_calc h3 {
	color: white;
	text-align: center;
}

.rate_column {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 10px;
}

.rate_column p {
	text-align: center;
	color: white;
	font-size: 20px;
	font-weight: bold;
}