.container {
	margin: 0 auto 40px;
}

.container h3 {
	text-align: center;
	font-size: 20px;
}

.kzt_currency {
	grid-template-columns: repeat(2, 1fr) !important;

	border: 1px solid #13334c;
	border-radius: 5px;

	padding: 10px 30px !important;
	margin: 10px auto 10px;

	max-width: 500px;
}

.kzt_currency p:last-child {
	text-align: end;
}

.currency_row {
	display: grid;
	grid-template-columns: 90px repeat(6, 1fr);
	align-items: center;
	gap: 10px;

	padding: 10px 20px;

	background: #434343;

	position: relative;
}

.currency_row:nth-child(2n) {
	background: #787878;
}

.currency_row button:first-child {
	position: absolute;
	animation-name: deleteBtn;
	animation-duration: .5s;
	right: -50px;
	opacity: 1;
}

@keyframes deleteBtn {
	from {
		opacity: .5;
		right: -30px;
	}

	to {
		opacity: 1;
		right: -50px;
	}
}

.currency_row:last-child {
	border-radius: 0 0 5px 5px;
}

.currency_row p {
	color: white;
	font-weight: bold;
	display: flex;
	gap: 8px;
	align-items: center;
}

.currency_row img {
	width: 45px;
}

.displayNone {
	display: none;
}

.currency_list {
	margin: auto;
}

.list_header {
	display: grid;
	grid-template-columns: 90px repeat(6, 1fr);
	align-items: center;
	gap: 10px;

	padding: 10px;
	border-radius: 5px 5px 0 0;

	background: #434343;

	padding: 10px 20px;
}

.list_header p {
	color: white;
	font-weight: bold;
}

.iconBtn svg {
	width: 30px;
	height: 30px;
}

.addBtn {
	color: #00af00 !important;
}

.deleteBtn {
	color: rgb(231, 43, 43) !important;
}

.cancelEditBtn {
	position: absolute;
	right: -50px;
	color: rgb(168, 168, 168)
}

.addBtn:hover {
	background: #00800017 !important;
	color: green !important;
}

.showAll {
	display: flex;
	gap: 5px;
	text-align: center;
	justify-content: center;

	padding: 5px;
	margin-top: 10px;

	border-radius: 10px;

	font-weight: bold;

	cursor: pointer;
}

.showAll:hover {
	background: #80808021;
}

.currency_row_edit_mode {
	cursor: pointer;

	transition: .3s;
}

.currency_row_edit_mode_active {
	background: #abd4ab !important;
}

.currency_row_edit_mode_active p {
	color: black;
}

.currency_row_edit_mode:hover {
	background: #333333;
}

.currency_row_edit_mode:nth-child(2n):hover {
	background: #5d5d5d;
}