.table_row {
	display: grid;
	grid-template-columns: repeat(10, 1fr);
	padding: 15px 5px;
	transition: .3s;
	border-bottom: 1px solid #80808052;
	gap: 5px;
	font-size: 12px;
	align-items: center;
}

.table_row:hover {
	background: #80808040;
	cursor: pointer;
}

.table_row:first-child p {
	font-weight: bold;
}

.table_row:first-child:hover {
	cursor: default;
	background: white;
}

.currency_container {
	display: flex;
	flex-direction: column;
	gap: 20px;
}

.kurs_container {
	display: flex;
	justify-content: center;
	gap: 12px;
}

.kurs_container span {
	font-size: 18px;
	font-weight: 600;
}

.textField {
	background: white,
}

.textField input {
	text-align: center;
	font-size: 18px;
	font-weight: bold;
}

.textField fieldset {
	text-align: center;
}

.textField label {
	width: 100%;
	text-align: center;
	transform-origin: top center;
	left: -13px;
	font-size: 20px;
}

.textField .Mui-disabled {
	color: "black !important",
}