.container {
	max-width: 750px;
	margin: 40px auto;
}

.container h3 {
	text-align: center;
	font-size: 20px;
}

.kzt_currency {
	border: 1px solid #13334c;
	border-radius: 5px;

	padding: 10px 30px !important;
	margin: 10px 0 20px;
}

.currency_row {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 50px;
	align-items: center;

	padding: 10px;

	background: white;

	border-bottom: 1px solid #13334c;
}

.currency_row:last-child {
	border-bottom: none;
}

.currency_row p:nth-child(2) {
	color: #13334c;
	display: flex;
	gap: 8px;
	align-items: center;
}

.currency_row img {
	width: 45px;
}

.currency_list {
	background: white;

	border-radius: 5px;
	border: 1px solid #13334c;

	padding: 0 20px 10px;
}

.list_header {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 50px;

	border-bottom: 1px solid #13334c;
	padding: 10px;
}

.list_header p {
	color: #13334c;
	font-weight: bold;
}

.showAll {
	display: flex;
	gap: 5px;
	text-align: center;
	justify-content: center;

	padding: 5px;
	margin-top: 10px;

	border-radius: 10px;

	font-weight: bold;

	cursor: pointer;
}

.showAll:hover {
	background: #80808021;
}