.currency_list_container {
	background: white;

	border-radius: 5px;
	border: 1px solid black;
	overflow: hidden;

	width: 700px;
	margin: auto;
}

.currency_list_row {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	/* grid-template-columns: minmax(80px, 180px) minmax(65px, 1fr) minmax(70px, 1fr); */
	/* grid-template-columns: minmax(80px, 180px) minmax(65px, 1fr) minmax(70px, 1fr) 150px; */
	gap: 10px;
	align-items: center;

	padding: 10px 20px;

	background: #434343;
	/* background: #3d3d3d; */
}

.header_row {
	font-size: 20px;
}

.currency_flag_img>div>p:first-child {
	font-weight: bold;
	font-size: 20px;
}

.currency_flag_img>div>p:last-child {
	font-size: 12px;
}

.row>p {
	font-size: 25px;
	font-weight: bold;
}

.currency_list_row:nth-child(2n) {
	background: #787878;
	/* background: #1C2021; */
}

.currency_list_row:hover {
	background: #e9e9e9,
}

.currency_list_row img {
	width: 45px;
	height: 30px;
}

.currency_list_row:first-child {
	cursor: unset;
}

.currency_list_row:first-child:hover {
	background: unset,
}

.currency_list_row:first-child p {
	font-weight: bold;
}

.currency_flag_img {
	display: flex;
	align-items: center;
	gap: 5px;
}

.showAll {
	display: flex;
	gap: 5px;
	text-align: center;
	justify-content: center;

	padding: 5px;
	margin-top: 10px;

	border-radius: 10px;

	font-weight: bold;

	cursor: pointer;
}

.showAll:hover {
	background: #80808021;
}

@media (max-width: 900px) {
	.currency_list_container {
		width: 100%;
	}
}

@media (max-width: 600px) {
	.header_row {
		font-size: 16px;
	}

	.currency_list_row {
		padding: 5px 10px;
	}

	.currency_list_row img {
		width: 30px;
		height: 20px;
	}

	.currency_flag_img>div>p:first-child {
		font-size: 17px;
	}

	.currency_flag_img>div>p:last-child {
		font-size: 10px;
	}

	.row>p {
		font-size: 20px;
		font-weight: bold;
	}
}

@media (max-width: 500px) {
	.currency_list_row {
		grid-template-columns: 1fr 90px 90px;
	}
}

@media (max-width: 400px) {
	.currency_list_row {
		grid-template-columns: 1fr 70px 70px;
	}
}