.background_color {
	/* background-color: #1C2021; */
	color: white;
	min-height: 100vh;
	padding-left: 300px;
}

.container {
	max-width: 1280px;
	width: 100%;

	margin: 0 auto;
	padding: 32px 100px;

	overflow: hidden;
}

.logoContainer {
	display: flex;
	justify-content: end;
}

.logo {
	display: none;
}

.container h1,
.container h2 {
	text-align: center;
	color: #e25500;
}

.container h1 {
	margin-bottom: 24px;
}

.currencyContainer {
	display: grid;
	grid-template-columns: 1fr;
	/* grid-template-columns: 1fr 1fr; */
	gap: 0 20px;
}

.currencyContainer>p {
	grid-column: span 2;
	justify-self: center;

	font-size: 22px;
	font-weight: bold;

	margin-bottom: 10px;
}

@media (max-width: 1200px) {
	.background_color {
		padding-left: 0;
	}

	.logoContainer {
		justify-content: space-between;
	}

	.logo {
		display: flex;
		height: 60px;
		position: relative;
		justify-content: center;
		align-items: center;
		width: 200px;
	}

	.logo img {
		width: 400px;
		position: absolute;
	}
}

@media (max-width: 900px) {
	.container {
		padding: 16px 60px;
	}

	.container h1 {
		margin-bottom: 0;
	}
}

@media (max-width: 700px) {
	.container {
		padding: 16px 10px;
	}

	.container h1 {
		margin-bottom: 0;
	}
}

