.container {
	margin-top: 30px;
	background: white;
	padding: 20px;
	border-radius: 10px;
}

.container h2 {
	text-align: center;
	font-weight: bold;
	font-size: 22px;
	margin-bottom: 10px;
}

.table_row {
	display: grid;
	grid-template-columns: 70px 1fr;
	padding: 15px 5px;
	transition: .3s;
	border-bottom: 1px solid grey;
	border-radius: 5px;
}

.table_row:hover {
	background: #80808040;
	cursor: pointer;
}

.table_row:first-child p {
	font-weight: bold;
}

.table_row:first-child:hover {
	cursor: default;
	background: white;
}