.container {
	background-color: #13334c;
}

.innerContainer {
	max-width: 1280px;
	position: relative;
	margin: 0 auto;
	width: 100%;
	padding: 32px 100px;
	box-sizing: border-box;

	display: grid;
	grid-template-columns: 2fr 1fr;
}

.leftBlock {
	display: flex;
	flex-direction: column;
	gap: 24px;
}

.blockItem {
	display: flex;
	align-items: center;
	gap: 12px;
	color: #e25500;
}

.blockItem p {
	color: white;
}

@media (max-width: 1200px) {
	.innerContainer {
		max-width: unset;
	}
}

@media (max-width: 900px) {
	.innerContainer {
		padding: 16px 20px;
	}
}

@media (max-width: 600px) {
	.innerContainer {
		grid-template-columns: 1fr;
	}
}