.table_row {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	padding: 15px 5px;
	transition: .3s;
	border-bottom: 1px solid grey;
}

.table_row:hover {
	background: #80808040;
	cursor: pointer;
}

.table_row:first-child p {
	font-weight: bold;
}

.table_row:first-child:hover {
	cursor: default;
	background: white;
}