.reserve_info {
	max-width: 600px;

	margin: auto;

	display: grid;
	gap: 20px;
}

.reserve_block {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 8px;
}

.text {
	font-size: 24px;
	font-weight: bold;
}

.number {
	background: #005792;

	color: white;
	font-size: 32px;

	border-radius: 5px;

	padding: 0 20px;
}

.number span {
	font-size: 25px;
}

@media (max-width: 900px) {
	.text {
		font-size: 20px;
	}

	.number {
		font-size: 25px;
	}

	.number span {
		font-size: 20px;
	}
}

@media (max-width: 600px) {
	.reserve_info {
		padding: 20px 10px;
	}
}